.container {
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
    gap: 40px;
    -webkit-gap: 40px;
    -moz-gap: 40px;
}
.image {
    width: 515px;
    background-color: rgb(182, 182, 182);
    border-radius: 15px;
}
.title {
    display: flex;
    flex-direction: column;
    max-width: 800px;
    text-align: center;
    gap: 20px;
    -webkit-gap: 20px;
    -moz-gap: 20px;
}
.dopTitle {
    color: rgba(255, 255, 255, 0.8);
}
.title_Selection::selection {
    color: var(--white);
    background-color: var(--blue);
}
.title_Selection::-moz-selection {
    color: var(--white);
    background-color: var(--blue);
}

@media screen and (max-width: 548px) {
    .image {
        transform: scale(0.9);
        -webkit-transform: scale(0.9);
        -moz-transform: scale(0.9);
    }
}
@media screen and (max-width: 515px) {
    .image {
        width: 400px;
    }
}
@media screen and (max-width: 490px) {
    .image {
        transform: scale(0.9);
        -webkit-transform: scale(0.9);
        -moz-transform: scale(0.9);
    }
}
@media screen and (max-width: 400px) {
    .image {
        width: 350px;
    }
}