/* Fonts */
@import url('https://fonts.googleapis.com/css2?family=Cuprum:wght@700&display=swap');

*,
*::before,
*::after {
  padding: 0;
}
ul[class],
ol[class] {
  padding: 0;
}
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  -webkit-scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1;
  background-color: var(--black);
}
ul[class],
ol[class] {
  list-style: none;
}
ul {
  padding: 0 0;
}
li {
  list-style-type: none;
}
a:not([class]) {
  text-decoration-skip-ink: auto;
}
img {
  max-width: 100%;
  display: block;
}
article > * + * {
  margin-top: 1em;
}
input,
button,
textarea,
select {
  font: inherit;
}
span {
  margin: 0;
}
h1 {
  font-family: 'Cuprum', sans-serif;
  font-size: 48px;
  color: var(--white);
  line-height: 1;
}
h3 {
  font-family: 'Cuprum', sans-serif;
  font-size: 18px;
  color: var(--white);
  line-height: 1;
}
h3::selection {
  color: var(--white);
  background-color: var(--darkViolet);
}
h3::-moz-selection {
  color: var(--white);
  background-color: var(--darkViolet);
}
h4 {
  font-family: Arial, sans-serif;
  font-size: 15px;
  color: var(--white);
  font-weight: 400;
  line-height: 1.2;
}
h4::selection {
  color: var(--white);
  background-color: var(--darkViolet);
}
h4::-moz-selection {
  color: var(--white);
  background-color: var(--darkViolet);
}
h5 {
  font-family: Arial, sans-serif;
  font-size: 11px;
  color: var(--white);
  font-weight: 400;
  line-height: 1;
  margin: 0;
}
h5::selection {
  color: var(--white);
  background-color: var(--darkViolet);
}
h5::-moz-selection {
  color: var(--white);
  background-color: var(--darkViolet);
}
@media screen and (max-width: 644px) {
  h1 {
    font-size: 40px;
  }
}
@media screen and (max-width: 514px) {
  h4 {
    font-size: 16px;
  }
}

:root {
  --black: #000;
  --white: #fff;
  --magenta: #F72585;
  --violet: #A83DEE;
  --darkViolet: #511BCD;
  --blue: #4361EE;
  --lightBlue: #4CC9F0;
}
