.cards__wrapper {
    display: flex;
    gap: 40px;
    -webkit-gap: 40px;
    -moz-gap: 40px;
}
.wrapper {
    width: 340px;
    height: 460px;
    background-color: #121212;
    border-radius: 20px;
    border: 1px solid #2C2C2C;
    padding: 23px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    aspect-ratio: 340/460;
}
.wrapper:hover {
    cursor: pointer;
}
.image {
    aspect-ratio: 25/28;
    width: 340px;
    height: 380px;
    background-color: var(--black);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.image_inner {
    width: 160px;
    height: 160px;
    aspect-ratio: 1/1;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.title {
    margin: 0 0 10px 10px;
}
.title h5 {
    color: rgba(255, 255, 255, 0.5);
    margin-bottom: 5px;
}
@media screen and (max-width: 1284px) {
    .cards__wrapper {
        height: 406px;
    }
}