.section__wrap {
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    gap: 30px;
    -webkit-column-gap: 30px;
}
.wrapper {
    display: flex;
    gap: 30px;
}
.image {
    width: 75px;
    height: 75px;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    -webkit-flex-direction: column;
    align-items: center;
}
.text {
    max-width: 442px;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    gap: 10px;
}
.text h4 {
    color: rgba(255, 255, 255, 0.8);
}

@media screen and (max-width: 1405px){
    .section__wrap {
        display: grid;
        grid-template-columns: 1fr 1fr;
        -ms-grid-columns: 1fr 1fr;
        margin-top: 60px;
    }
}
@media screen and (max-width: 1200px){
    .section__wrap {
        display: flex;
        flex-direction: column;
        -webkit-flex-direction: column;
        align-items: center;
    }
}
@media screen and (max-width: 578px){
    .text {
        max-width: 350px;
    }
}
@media screen and (max-width: 490px){
    .text {
        max-width: 290px;
    }
}
@media screen and (max-width: 430px){
    .wrapper {
        display:flex;
        flex-direction: column;
        -webkit-flex-direction: column;
        align-items: center;
        text-align: center;
        margin-top: 0;
    }
    .section__wrap {
        gap: 60px;
    }
}
@media screen and (max-width: 414px){
    .wrapper {
        margin-top: 0;
    }
}