.container {
    margin: 100px 0;
    overflow: hidden;
}
.title_Selection::selection {
    color: var(--white);
    background-color: var(--violet);
}
.title_Selection::-moz-selection {
    color: var(--white);
    background-color: var(--violet);
}
.Tabs {
    margin-top: 20px;
}
.wrapper {
    display: flex;
}
.content__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    -ms-grid-columns: 1fr 1fr;
    min-width: 100%;
}
.first_section {
    max-width: 480px;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    justify-content: center;
}
.section_bottom_text {
    margin: 10px 0 30px 0;
}
.divider {
    height: 1px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.8);
    margin: 40px 0;
}
.logo {
    display: flex;
}
.logo_title {
    font-family: Arial, sans-serif;
    font-weight: 800;
    position: relative;
    top: 9px;
}

.second_section {
    padding: 50px;
}
.image {
    background-color: rgb(217, 217, 217);
    width: 100%;
    height: -webkit-fit-content;
    aspect-ratio: 1/1;
    border-radius: 15px;
    overflow: hidden;
}

@media screen and (max-width: 870px) {
    .content__wrapper {
        display: flex;
        flex-direction: column-reverse;
        -webkit-flex-direction: column-reverse;
    }
}
@media screen and (max-width: 555px) {
    .divider {
        margin: 20px 0;
    }
}