.ultra_wrapper {
    height: 730px;
}
.wrapper {
    display: grid;
    grid-template-columns: 1fr 2fr;
    -ms-grid-columns: 1fr 2fr;
    padding: 40px 0;
}
.title {
    width: 400px;
    z-index: 5;
    background-image: linear-gradient(90deg, #000 50%, transparent);
    background-image: -webkit-gradient(linear, left top, right top, color-stop(50%, #000), to(transparent));
    background-size: 120% 100%;
    background-position: right;
    height: 555px;
}
.title::after {
    content: '';
    background-color: var(--black);
    width: 1000px;
    height: 555px;
    display: inline-block;
    position: relative;
    top: -330px;
    right: auto;
    left: -999px;
    z-index: -1;
}
.buttons {
    display: flex;
    gap: 15px;
    -webkit-gap: 15px;
    -moz-gap: 15px;
    margin-top: 80px
}
.button {
    background-color: transparent;
    border: 1px solid var(--white);
    padding: 30px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    align-items: center;
}
.button:hover {
    cursor: pointer;
}
.cards {
    position: relative;
    top: -610px;
    aspect-ratio: 441/127;
    height: 513px;
    box-sizing: initial;
    -webkit-box-sizing: initial;
    -moz-box-sizing: initial;
    padding: 40px 0;
    z-index: 2;
}
.title_Selection::selection {
    color: var(--white);
    background-color: var(--magenta);
}
.title_Selection::-moz-selection {
    color: var(--white);
    background-color: var(--magenta);
}

@media screen and (max-width: 1412px) {
    .cards {
        transform: scale(0.8);
        -webkit-transform: scale(0.8);
        -moz-transform: scale(0.8);
        top: -620px;
    }
}
@media screen and (max-width: 1360px) {
    .cards {
        padding: 50px;
    }
}
@media screen and (max-width: 1334px) {
    .cards {
        padding: 70px;
        top: -650px
    }
}
@media screen and (max-width: 1270px) {
    .cards {
        top: -700px;
        height: 553px;
    }
}
@media screen and (max-width: 840px) {
    .ultra_wrapper {
        height: 900px;
    }
    .wrapper {
        display: flex;
        flex-direction: column;
        -webkit-flex-direction: column;
        align-items: center;
        justify-content: center;
        -webkit-justify-content: center;
    }
    .title {
        text-align: center;
        background: inherit;
        height: auto;
    }
    .title::after {
        display: none;
    }
    .cards {
        top: -100px;
        left: -220px;
    }
    .buttons {
        display: none;
    }
}
