.wrapper {
    display: flex;
    align-items: center;
    -webkit-align-items: center;
    gap: 10px;
    width: 216px;
}
.wrapper:hover {
    cursor: pointer;
}
.circle__wrapper {
    width: 25px;
    height: 25px;
    background-color: var(--violet);
    border-radius: 50%;
    color: var(--white);
    overflow: hidden;
    display: flex;
    justify-content: flex-end;
    -webkit-justify-content: flex-end;
    align-items: center;
}
.arrows {
    display: flex;
    justify-content: center;
    -webkit-justify-content: center;
    align-items: center;
    -webkit-align-items: center;
    gap: 30px;
    padding: 0 3px;
}
.text {
    font-size: 15px;
}