.container {
    margin: 100px 0;

}
.wrapper {
    display: grid;
    grid-template-columns: 2fr 1fr;
    -ms-grid-columns: 2fr 1fr;
    gap: 100px;
    -webkit-gap: 100px;
    -moz-gap: 100px;
}
.button {
    margin-top: 50px;
}
.link {
    margin-top: 30px;
}
.title_Selection::selection {
    color: var(--white);
    background-color: var(--lightBlue);
}
.title_Selection::-moz-selection {
    color: var(--white);
    background-color: var(--lightBlue);
}

@media screen and (max-width: 1050px) {
    .card_anim {
        display: flex;
        align-items: center;
    }
}
@media screen and (max-width: 960px) {
    .wrapper {
        display: flex;
        flex-direction: column-reverse;
        -webkit-flex-direction: column-reverse;
        gap: 50px;
        -webkit-gap: 50px;
        -moz-gap: 50px;
    }
    .button {
        display: flex;
        justify-content: flex-end;
    }
    .link {
        display: flex;
        justify-content: flex-end;
    }
}
@media screen and (max-width: 775px) {
    .button {
        display: flex;
        justify-content: center;
    }
    .link {
        display: flex;
        justify-content: center;
    }
}