.footer::before {
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.4);
    display: inline-block;
    position: relative;
    top: 0;
    left: 0;
    margin-bottom: 30px;
}
.social_footer::before {
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.4);
    display: inline-block;
    position: relative;
    top: 0;
    left: 0;
    margin: 20px 0;
}
.wrapper {
    display: flex;
    justify-content: space-between;
}
.menu {
    color: rgba(255, 255, 255, 0.8);
    font-family: Arial, sans-serif;
}
.menu ul {
    display: flex;
    flex-direction: column;
    gap: 12px;
    -webkit-gap: 12px;
}
.menu ul li h4 {
    position: relative;
    left: -10px;
    font-weight: 800;
    color: var(--white);
}
.menu ul li:hover {
    cursor: pointer;
}
.social {
    margin: 0 0 20px 0;
    width: 100%;
    display: flex;
    justify-content: flex-end;
}
.social_icons {
    display: flex;
    gap: 10px;
    -webkit-gap: 10px;
    -moz-gap: 10px;
}

@media screen and (max-width: 830px) {
    .menu {
        display: none;
    }
    .wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .social {
        display: flex;
        align-items: center;
        justify-content: center;
    }
}