.button {
    padding: 16px 45px;
    border-radius: 15px;
    border: 0 transparent;
    background: rgb(76,201,240);
    background: linear-gradient(90deg, rgba(76,201,240,1) 0%, rgba(168,61,238,1) 100%);
    box-shadow: 0 0 35px 2px var(--blue), 0 0 7px var(--lightBlue);
    -webkit-box-shadow: 0 0 35px 2px var(--blue), 0 0 7px var(--lightBlue);
    -moz-box-shadow: 0 0 35px 2px var(--blue), 0 0 7px var(--lightBlue);
    transition: box-shadow .2s ease;
    -webkit-transition: box-shadow .2s ease;
    -moz-transition: box-shadow .2s ease;
    z-index: 10;
}
.button:hover {
    cursor: pointer;
    box-shadow: 0 0 10px 1px var(--blue), 0 0 7px var(--lightBlue);
    -webkit-box-shadow: 0 0 10px 1px var(--blue), 0 0 7px var(--lightBlue);
    -moz-box-shadow: 0 0 10px 1px var(--blue), 0 0 7px var(--lightBlue);
}

/* Red button*/
.button_red {
    padding: 16px 45px;
    border-radius: 15px;
    border: 0 transparent;
    background: var(--magenta);
    background: linear-gradient(90deg, var(--magenta) 0%, var(--violet) 100%);
    box-shadow: 0 0 35px 2px var(--magenta), 0 0 7px var(--magenta);
    -webkit-box-shadow: 0 0 35px 2px var(--magenta), 0 0 7px var(--magenta);
    -moz-box-shadow: 0 0 35px 2px var(--magenta), 0 0 7px var(--magenta);
    transition: box-shadow .2s ease;
    -webkit-transition: box-shadow .2s ease;
    -moz-transition: box-shadow .2s ease;
    z-index: 10;
}
.button_red:hover {
    cursor: pointer;
    box-shadow: 0 0 10px 1px var(--magenta), 0 0 7px var(--magenta);
    -webkit-box-shadow: 0 0 10px 1px var(--magenta), 0 0 7px var(--magenta);
    -moz-box-shadow: 0 0 10px 1px var(--magenta), 0 0 7px var(--magenta);
}
