.navBar__wrapper {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    z-index: 20;
    border-bottom: 1px solid rgba(255, 255, 255, 0.6);
}
.wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0;
    z-index: 21;
}
.navigation {
    display: flex;
}
.logo {
    margin-right: 90px;
    cursor: pointer;
}
.navigation__inner ul{
    display: flex;
}
.navigation__inner ul li {
    margin-right: 50px;
}
.navigation__inner ul li h4 {
    transition: .2s ease;
    -webkit-transition: .2s ease;
    cursor: pointer;
}
.navigation__inner ul li:hover h4{
    cursor: pointer;
    color: var(--lightBlue);
}
.to__app {
    transition: .2s ease;
    -webkit-transition: .2s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--white);
}
.to__app h4 {
    transition: .2s ease;
    -webkit-transition: .2s ease;
    margin-right: 5px;
}
.to__app:hover {
    cursor: pointer;
    color: var(--lightBlue);
}
.to__app h4:hover {
    cursor: pointer;
    color: var(--lightBlue);
}
.devises__navigation {
    display: none;
}
.navDevises {
    transition: .2s ease;
    -webkit-transition: .2s ease;
}
.navDevises:hover {
    color: var(--lightBlue);
}
.menu__icon {
    color: transparent;
    display: none;
}

@media screen and (min-width: 1215px) {
    .menu__icon {
        color: transparent;
        display: none;
    }
}
@media screen and (max-width: 1215px) {
    .menu__icon {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .navigation__inner {
        display: none;
    }
    .wrapper {
        padding: 3px 0;
    }
    .navBar__wrapper {
        height: 66px;
    }
    .navigation {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 10px 0;
    }
    .to__app {
        display: none;
    }
    /* =========== Navigation ========== */
    .devises__navigation {
        position: fixed;
        width: 100%;
        display: flex;
        flex-direction: column;
        -webkit-flex-direction: column;
        align-items: flex-end;
        padding: 0 24px;
        background-color: rgba(0, 0, 0, 0.8);
        z-index: 10;
        box-sizing: border-box;
    }
    .devises__navigation::after {
        content: ' ';
        width: 120%;
        height: 1px;
        background-color: rgba(255, 255, 255, 0.6);
        position: relative;
        left: 20px;
    }
    .navBar__wrapper::after {
        top: -22px;
    }
    .devises__navigation ul {
        display: flex;
        flex-direction: column;
        -webkit-flex-direction: column;
        gap: 30px;
        -webkit-gap: 30px;
        -moz-gap: 30px;
    }
    .dev__nav__wrapper {
        width: 100%;
        height: 100vh;
    }
    .dev__nav__wrapper_closed {
        width: 0;
        height: 0;
    }
}