.title_Selection::selection {
    color: var(--white);
    background-color: var(--magenta);
}
.title_Selection::-moz-selection {
    color: var(--white);
    background-color: var(--magenta);
}
.wrapper {
    margin-top: 60px;
}
.section__wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.content {
    display: flex;
    justify-content: space-between;
    max-width: 1800px;
}

@media screen and (max-width: 1405px) {
    .content {
        flex-direction: column;
    }
}