.title {
    max-width: 600px;
    min-width: 398px;
}
.dop__title {
    color: rgba(255, 255, 255, 0.8);
    margin-top: 20px;
}
.upper__wrapper {
    margin-bottom: 20px;
    border-radius: 5px;
    padding: 5px 10px;
    background-color: var(--violet);
    max-width: fit-content;
}
.upper_Selection::selection {
    color: var(--white);
    background-color: var(--violet);
}

@media screen and (max-width: 415px) {
    .title {
        min-width: 350px;
    }
}